#App {
    width: 1000px;
    margin: 0 auto;
    padding: 30px;
}

#App main {
    padding: 30px;
    position: relative;
}

#App main #General {
    margin-bottom: 15px;
}
#App main #General .name {
    text-align: center;
}

#App main #General .title {
    text-align: center;
    font-size: 20px;
    margin-bottom: 5px;
}

#App main #General .gen-info {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-content: center;
    align-items:center;
}

#App main #General .email {
    flex: .1 1 auto;
}

#App main #General .linked-in {
    flex: .1 1 auto;
}

#App main #General .location {
    flex: .1 1 auto;
}

#App main #General .phone-num {
    flex: .1 1 auto;
}

#App main #General .edit-form {
    top: 0;
}

#App main #General .edit-toggle {
    display: none;
    position: absolute;
    top: 50px;
    right: 50px;
}

#App main #General:hover .edit-toggle {
    display: block;
}

#App main #Education {
    position: relative;
    min-height: 100px;
}

#App main #Education > div {
    display: grid;
    grid-template-columns: 200px auto;
    grid-column-gap: 40px;
    margin-bottom: 20px;
}

#App main #Education > div:not(:last-child) {
    padding-bottom: 5px;
    margin-bottom: 5px;
}

#App main #Education .education-date {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: 5px;
    text-align: right;
    color:#808080;
    margin-right: 10px;
}

#App main #Education .education-degree {
    font-weight: 400;
    font-size: 16px;
    color:#808080;
    margin-left: 10px;
}

#App main #Education .education-school {
    font-style: italic;
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 10px;
    margin-left: 10px;
}

#App main #Education .education-location {
    font-style: italic;
    text-align: right;
    margin-bottom: 10px;
    margin-right: 10px;
}

#App main #Education .edit-toggle {
    display: none;
    position: absolute;
    top: 50px;
    right: -15px;
}

#App main #Education:hover .edit-toggle {
    display: block;
}
 
