.edit-form {
    position: absolute;
    top: 60px;
    right: 0;
    background-color: #82AFB4;
    color: #FFFFFF;
    padding: 20px;
    width: 300px;
    z-index: 20;
  }
  .edit-form input, .edit-form textarea {
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.5);
    background: none;
    border-radius: 0;
    margin-bottom: 10px;
    font-family: priori-sans, sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 15px;
    line-height: 1.4;
    padding: 5px 10px;
    color: #FFFFFF;
  }
  .edit-form button {
    background-color: #FFFFFF;
    color: #82AFB4;
  }
